* {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
body {
  padding: 60px;
  background-color: green;
}
input {
  font-size: 1.2rem;
  width: 100%;
  color: gray;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 10px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  width:fit-content;
  background-color: white;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
  width:100%;
}
h1{
  text-align:  center;
  padding-bottom: 1rem;
}

.card-container{
  justify-content: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
